.home {

    .homeContent {
        background: $color-1;// linear-gradient(131deg, $color-1 49%, rgb(158, 186, 218) 100%);
        min-height: $minHeight;
        width: 100%;
        border-radius: $borderRadius;
        position: relative;
        overflow: hidden;

        &::after {
            content: '';
            position: absolute;
            height: 100%;
            width: inherit;
            // background: url('../../media') no repeat center/cover;
            z-index: -1;
            filter: brightness(30%)
        }

        .content {
            position: absolute;
            top: 20%;
            left:15%;
            font-family: $font-0;

            #title {
                font-size: 5rem;
                font-family: $font-0;
                color: $white;
                text-transform: uppercase;
                text-shadow: 3px 6px 2px $color-6;
                margin-right: 5%;
                // font-size: 5rem;
                // padding: 1.5rem 0 2rem;
                // color: $color-2;
                // text-shadow: 1px 4px 1px $color-6;
            }

            #presentation {
                font-size: 1rem;
                text-align: justify;
                margin-right: 20%;
                padding: 1rem 0 2.5rem;
                color: $white;
            }

            .pdf {
                width: 100%;
                justify-content: left;//space-around;
                text-align: center;
                display: grid;
                grid-auto-rows: "1"
                                "2"
                                "3";

                a {
                    font-size: 1.2rem;
                    // color: $white;
                    background: $color-4;
                    display: block;
                    padding: 0.7rem 3rem;
                    border-radius: $borderRadius;
                    box-shadow: 0 5px 1px $color-6;
                    transition: 0.2s ease;
                    
                    &:hover {
                        // color: $black;
                        transition: 0.2s ease;
                        background: $color-5;
                        // box-shadow: 0 5px 1px $color-6;
                    }
                }

                .cv {
                    grid-area: 1;
                    margin-bottom: 1rem;
                }
                .skillsAssessment {
                    grid-area: 2;
                    margin-bottom: 1rem;
                }
            }

            .certificates {
                font-size: 1rem;
                text-align: justify;
                margin-top: 10%;
                margin-right: 10%;
                padding: 1rem 0 4rem;
                color: $white;
                margin-right: 20%;

                #titleCert {
                    font-size: 1.5rem;
                    font-family: $font-0;
                    color: $white;
                    text-transform: uppercase;
                    text-shadow: 3px 6px 2px $color-6;
                    // margin-right: 20%;
                    text-align: left;
                }

                #opquast,
                #degree {
                    color: $white;
                }

            }
        }
    }
}

@media screen and (max-width: 850px) {
    .homeContent {
        margin: 0 !important;

    }
}

@media screen and (max-width: 700px) {
    .homeContent {
        .content {
            // position: absolute;
            top: 15%;
            left:15%;
            #title {
                font-size: 2.5rem! important;
            }
            .pdf{
                a {
                    font-size: 1.3rem! important;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .homeContent {
        .content {
            position: absolute;
            top: 14% !important;
            left:15% !important;
            .pdf{
                a {
                    font-size: 1.2rem! important;
                }
            }
        }
    }
}

@media screen and (max-width: 550px) {
    .homeContent {
        .content {
            top: 12% !important;
            left:15% !important;
            .pdf{
                a {
                    font-size: 1.1rem! important;
                }
            }
        }
    }
}
@media screen and (max-width: 500px) {
    .homeContent {
        .content {
            top: 10% !important;
            left:15% !important;
            .pdf{
                a {
                    font-size: 1rem! important;
                }
            }
        }
    }
}

@media screen and (max-width: 420px) {
    .homeContent {
        .content {
            top: 7% !important;
            left:15% !important;
            .pdf{
                a {
                    font-size: 0.9rem! important;
                }
            }
        }
    }
}
@media screen and (max-width: 360px) {
    .homeContent {
        .content {
            top: 5% !important;
            left:15% !important;
            #title {
                font-size: 2rem! important;
            }
            .pdf{
                margin-top: -2% !important;
                a {
                    font-size: 0.7rem! important;
                }
            }
            .certificates {
                margin-top: -2% !important;
            }
        }
    }
}

@media screen {
    .homeContent {
        // &:after {
        //     background: url('../../.jpg') no-repeat 60% 50%/cover !important;
        // }
        
        #name {
            font-size: 3rem !important;
        }
        #title {
            font-size: 3rem !important;//1.8rem
        }
    }
}